* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  line-height:30px;
}

/* 헤더 전체 블럭 */
header {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.07), 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  color: #212529;  
  background-color: #3d30fb;
  color: #f2f2f2;
  height: 75px;
  width: 100%;
  position: fixed;
  z-index: 900;
}

/* 로고 그림부터 끝까지 블럭 */
.nav-area {
  display: flex;
  align-items: center;
  margin: 0 auto;
}

/* 로고 텍스트 */
.logo {
  text-decoration: none;
  font-size: 25px;
  color: inherit;
  margin: 20px 20px 20px 20px;
  font-weight: bold;
  color: #ffffff;
}

/* 메뉴 불럭 */
.menus {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  list-style: none;
  background-color: transparent;
}

/* 첫 메뉴 텍스트 */
.menu-items {
  position: relative;
  font-size: 17px;
  /* margin-top: 10px; */
  /* margin-bottom: 10px; */
  margin-left: 5px;
  /* margin-right: 10px; */
  color: #ffffff;
}

.menu-items a {
  display: block;
  font-size: 17px;
  font-weight: 600; /* 메인, 첫째 서브 메뉴 글자 */
  color: inherit;
  text-decoration: none;
  margin-top: 0px;
}

.menu-items button {
  display: flex;
  align-items: center;
  color: inherit;
  font-size: 17px;
  font-weight: 600; /* 두번째 서브 메뉴 글자 */
  border: none;
  background-color: transparent;
  cursor: pointer;
}

/* 삼각형 */
button span {
  margin-left: 3px; /* 첫 매뉴글자와 삼각의 간격*/
  margin-top: 5px;
}

.menu-items > a, .menu-items button {
  text-align: left;
  padding-left: 10px; /* 헤더 메뉴 간 간격*/
}

/* 메뉴에 마우스 올라 갈 때 */
.menu-items a:hover,
.menu-items button:hover {
  background-color: #bbc8fb;
}

.arrow::after {
  content: "";
  display: inline-block;
  margin-left: 0.28em;
  vertical-align: 0.09em;
  border-top: 0.42em solid;
  border-right: 0.32em solid transparent;
  border-left: 0.32em solid transparent;
}

/* 상자 */
.dropdown {
  position: absolute;
  right: 0;
  left: 10px;/* 위 메뉴 좌 위치로부터 이격*/
  box-shadow: 0 10px 15px -3px rgba(46, 41, 51, 0.08),
    0 4px 6px -2px rgba(71, 63, 79, 0.16);
  font-size: 17px;
  z-index: 9999;
  min-width: 18rem;
  /* padding: 2px 20px 2px 3px; */
  list-style: none;
  background-color: #3849de;
  border-radius: 0.5rem;
  display: none;
}

.dropdown.show {
  display: block;
}

.dropdown .dropdown-submenu {
  position: absolute;
  left: 100%;
  top: -10px;
  font-size: 17px;
}

/* content */
.content {
  /* max-width: 2000px; */
  margin: 0 auto;
  padding: 8rem 3rem 3rem 3rem; /*  상 좌 우 하 */
}


.content h2 {
  margin-bottom: 1rem;
}

.content a {
  color: #cc3852;
  margin-right: 10px;
}

ul {
  padding-left: 2rem 0rem;
}

ol {
  padding-left: 2rem 0rem;
}
p {
  padding-left: 2rem;
}
h1, h2, h3, h4, h5, h6, img {
  padding-left: 0rem;
}
p {
  padding-left: 2rem;
}