hr.class-1 {
  margin-top: 10px;
  margin-bottom: 10px;
  border-top: 5px solid rgb(110, 144, 238)
}

hr.class-2 {
  border-top: 3px double rgb(110, 144, 238)
}

hr.class-3 {
  border-top: 1px dashed  rgb(110, 144, 238)
}

hr.class-4 {
  border-top: 1px dotted rgb(110, 144, 238)
}

hr.class-5 {
  background-color: #fff;
  border-top: 2px dashed rgb(110, 144, 238)
}

hr.class-6 {
  background-color: #fff;
  border-top: 5px dotted rgb(0, 0, 0)
}


